.GameWindow {
    width: 1500px;
    height: 850px;
    overflow: hidden;
}

.DicePanel {
    display: block;
    margin:auto;
}

.DiceImages {
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
}

.DiceButtonPanel {
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width:100%;
}

.DiceButton {
    width: 90px;
    margin: 10px;
}